html {
  color-scheme: light dark;
}

layer {
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
}

layer.background {
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template: "background-image" 1fr
  / 1fr;
}

layer.background .background-image {
  background-position: bottom;
  background-size: cover;
  grid-area: background-image;
}

@media (prefers-color-scheme: dark) {
  layer.background .background-image {
    background-image: url("background.fc35d2e0.jpg");
  }
}

@media (prefers-color-scheme: light) {
  layer.background .background-image {
    background-image: url("background.6c38cfb6.jpg");
  }
}

layer.interact {
  grid-template: ". . ." 2%
                 ". title ."
                 ". . ."
                 / 1fr 10fr 1fr;
}

layer.interact .title {
  aspect-ratio: 1;
  grid-area: title;
  position: relative;
}

layer.interact .title .link {
  filter: invert(84%) sepia(89%) saturate(6858%) hue-rotate(356deg) brightness(99%) contrast(115%);
  transition: filter .5s ease-out;
  position: absolute;
}

@media (prefers-color-scheme: light) {
  layer.interact .title .link:hover {
    filter: invert();
  }
}

@media (prefers-color-scheme: dark) {
  layer.interact .title .link:hover {
    filter: invert(0);
  }
}

layer.interact .title .link.sell {
  width: 30%;
  top: 4%;
  left: 0%;
}

layer.interact .title .link.everything {
  width: 68%;
  top: 0%;
  right: 0%;
}

layer.decal {
  grid-template-columns: 1fr 10fr 1fr;
}

@media (prefers-color-scheme: light) {
  layer.decal {
    grid-template-rows: 5% auto auto;
    grid-template-areas: ". . ."
                         ". object ."
                         ". . .";
  }

  layer.decal .decal-image {
    display: none;
  }
}

@media (prefers-color-scheme: dark) {
  layer.decal {
    grid-template-rows: 10% 1fr;
    grid-template-areas: ". . ."
                         "image image image";
  }

  layer.decal .object-wrap, layer.decal .object-wrap #object {
    display: none;
  }
}

layer.decal .object-wrap {
  aspect-ratio: .9;
  pointer-events: none;
  grid-area: object;
  position: relative;
}

layer.decal .object-wrap #object {
  pointer-events: all;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

layer.decal .decal-image {
  background-image: url("sketch.4feb7822.png");
  background-position: 0 -50%;
  background-size: cover;
  grid-area: image;
}

layer.decal .background-reoverlay {
  background: url("bg-overlay.aa9e7d03.png") 100% 100% / 59vw 77.5vw no-repeat;
  grid-area: none;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
}

@media (prefers-color-scheme: light) {
  layer.decal .background-reoverlay {
    display: none;
  }
}
